import React from "react"
import Layout from "../components/global/global_layout"
import { graphql } from "gatsby"
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import ColumnOf2 from "../components/layout/column_of_2"
import KeyVisual from "../components/partial/key_visual"
import TagsCard from "../components/partial/tags_card"
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize, boxShadow} from '../util/style_property'
import TagDrops from '../components/partial/blog_article'
import BlogArticle from "../components/partial/blog_article"
import SideBar from "../components/partial/side_bar"

export const query = graphql`
  query($slug: String!){
    contentfulArticle(slug : {eq: $slug}){
      title
      updatedAt(formatString: "YYYY-MM-DD")
      body{
        json
      }
      tags{
        name
        slug
      }
      thumbnail{
        fluid{
          src
        }
      }
    }
  }
`

function BlogDetail(props) {
  const articleData = props.data.contentfulArticle
  const tags = props.data.contentfulArticle.tags
  const options = {
    renderNode: {
      "embedded-asset-block": (node) =>{
        return<img src={node.data.target.fields.file['en-US'].url}
                   alt={node.data.target.fields.title['en-US']} />
      }
    }
  }
  return (
    <Layout>
      <KeyVisual />
      <Wrapper>
        <ColumnOf2
          main={<BlogArticle articleData={articleData}/>}
          aside={<SideBar />}
        />
      </Wrapper>
    </Layout>
  )
}

const Wrapper = styled.div`
  width: 100%;
  padding: ${padding.m};
  margin: auto;
  background-color: ${color.background_dark_gray};
`

const BlogContentWrapper = styled.div`
  display: block;
  width: 100%;
  padding: ${padding.m};
  background-color: ${color.background_light_gray};
  border-radius: ${borderRadius.m};
  box-shadow: ${boxShadow.s};
`

export default BlogDetail