import React from "react"
import { graphql, useStaticQuery } from 'gatsby'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import styled from 'styled-components'
import BlogItem from './blog_item'
import TagDrops from './tag_drops'
import { color, padding, margin, borderRadius, textSize, boxShadow } from '../../util/style_property'

const BlogArticle = ({ articleData }) => {

  const Wrapper = styled.article`
    display: block;
    width: 100%;
    padding: ${padding.m};
    background-color: ${color.background_light_gray};
    border-radius: ${borderRadius.m};
    box-shadow: ${boxShadow.s};
  `

  const ArticleTitle = styled.h1`
    display: block;
    width: 100%;
    margin: 16px;
    font-size: ${textSize.h2};
    word-wrap: break-word;
  `

  const DateTime = styled.time`
    display:block;
    width: 100%;
    padding: 0;
    margin: 16px;
  `
  
  const TagDropsWrapper = styled.div`
    display: flex;
    width: 100%;
    flex-wrap: wrap;
  `

  const tags = articleData.tags
  const options = {
    renderNode: {
      "embedded-asset-block": (node) => {
        return <img src={node.data.target.fields.file['en-US'].url}
          alt={node.data.target.fields.title['en-US']} />
      }
    }
  }

  const ThumbnailWrapper = styled.div`
    width: 128px;
    height: 128px;
    padding: ${padding.s};
  `

  const Thumbnail = styled.img`
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: ${borderRadius.s};
  `

  const FlexRow = styled.div`
    display:flex;
    width: 100%;
  `

  const ContentWrapper = styled.div`
    width: 100%;
    padding: ${padding.l};
    margin: ${margin.s} auto;
    background-color: ${color.white};
    border-radius:${borderRadius.s};
    h3{
      box-sizing: border-box;
      width: 100%;
      margin-bottom:${margin.l};
      padding: ${padding.s} ${padding.s} ${padding.s} ${padding.m};
      border-left: ${padding.s} solid ${color.green};
      background-color: ${color.background_white_gray};
    }
    *+h3{
      margin-top: ${margin.xl};
    }
    ul,ol{
      padding: ${padding.m};
      margin: ${margin.s} 0;
      list-style-type: none;
      border: 2px solid ${color.green};
      border-radius: ${borderRadius.s};

      p{
        margin: 0;
      }
    }
    li{
      position:relative;
      padding-left: ${padding.m};
      &::before{
        content: "";
        position: absolute;
        top:0;
        left:0;
        bottom: 0;
        display: block;
        width: ${padding.s};
        height: ${padding.s};
        margin: auto;
        border-radius: 50%;
        background-color: ${color.green};
      }

      &+li{
        margin-top: ${margin.s};
      }
    }
  `

  const src = articleData.thumbnail.fluid.src

  const thumbnail = src ? (<ThumbnailWrapper><Thumbnail src={src} /></ThumbnailWrapper>) : null
  return (
    <Wrapper>
      <FlexRow>
        {thumbnail}
        <div>
          <ArticleTitle>{articleData.title}</ArticleTitle>
          <DateTime datetime={articleData.updatedAt}>{articleData.updatedAt}</DateTime>
        </div>
      </FlexRow>
      <TagDropsWrapper><TagDrops tags={tags} isButton={true} /></TagDropsWrapper>
      <ContentWrapper>{documentToReactComponents(articleData.body.json, options)}</ContentWrapper>
    </Wrapper>
  )
}

export default BlogArticle